*{
     margin: 0;
     box-sizing: border-box;
     color: white;
     font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.container{
    background-image: url('./image.png');
    background-size: 100% 100%;
    height: 100vh;
    width: 100vw;
    text-align: center;
}

textarea{
    width: 50vw;
    height: 5.5vh;
    background: transparent;
    border-style: solid;
    border-width:medium;
    border-color:white;
    margin: 9vh auto 0 auto;
    resize: none;
    font-size: 3vh;
    font-weight: 700;
    text-align: center;
}

textarea::placeholder{
    color: hsla(0, 0%, 82%, 0.549);
    font-weight: lighter;
}

.heading{
    font-size: 12vh;
    font-weight: 700;
    margin-top: 10vh;
}

.city-name{
    font-size: 7vh;

}

.temperature{
    font-size: 7vh;
}

.date{
    width: 90%;
    margin: auto;
    font-size: 3vh;
}

.credits{
    position: absolute;
    bottom: 1vh;
    left: 10%;
    right: 10%;
}

.fa-heart{
    color: red;
}